<template>
  <Layout class="law-list">
    <!-- <Button :disabled="!currentCategory" slot="extra" type="primary" size="small" @click.native="$router.push({ name: 'LawDetail', params: { type: 'new', id: 0, category: currentCategory.id } })">添加</Button> -->
    <Table highlight-row :columns="lawItemListColumns" :data="lawItemList"/>

    <Modal
      :mask-closable="false"
      title="编辑章节"
      v-model="addLawChapterModal"
      :loading="true"
      @on-cancel="closeModal"
      @on-ok="saveChapter">
      <Form :model="lawChapterData">
        <FormItem label="名称">
          <Input v-model="lawChapterData.title" />
        </FormItem>
      </Form>
    </Modal>
    <Modal
      :mask-closable="false"
      title="编辑法条"
      v-model="addLawItemModal"
      @on-cancel="closeModal"
      :loading="true">
      <Form :model="lawItemData">
        <FormItem label="标题">
          <Input :disabled="!editLawItemFlag" v-model="lawItemData.title" />
        </FormItem>
        <FormItem label="标签">
          <tagSelector :disabled="!editLawItemFlag" v-model="lawItemData.tagInfo" />
        </FormItem>
        <FormItem label="分值">
          <Input :disabled="!editLawItemFlag" v-model="lawItemData.score" />
        </FormItem>
        <FormItem label="排序">
          <Input :disabled="!editLawItemFlag" v-model="lawItemData.sort" />
        </FormItem>
        <FormItem label="内容">
          <Input :disabled="!editLawItemFlag" type="textarea" v-model="lawItemData.content" />
        </FormItem>
      </Form>

      <Row slot="footer" style="align-item: right">
        <Button v-if="editLawItemFlag" type="default" @click.native="closeModal">取消</Button>
        <Button v-if="editLawItemFlag" type="primary" @click.native="saveLawItem">确定</Button>
        <Button v-if="!editLawItemFlag" type="primary" @click.native="editLawItemFlag = true">编辑</Button>
      </Row>
    </Modal>
  </Layout>
</template>

<script>
import { getLawItemList, saveLawItem, deleteLawItem } from '@/api/law'
import tagSelector from '../components/tag-selector'

const LawItemModel = {
  categoryId: null, // 非必填
  content: '',
  documentLevel: 0,
  id: null,
  keywords: '',
  lawId: null,
  level: 0,
  parentId: null,
  score: 0,
  sort: 0,
  tagInfo: [],
  title: ''
}

export default {
  name: 'law-list',
  components: {
    tagSelector
  },
  data () {
    return {
      lawItemList: [],
      lawItemListColumns: [
        {
          type: 'index',
          width: 60,
          align: 'center'
        },
        // {
        //   title: '类别',
        //   key: 'category'
        // },
        {
          title: '名称',
          render: (h, { column, row, index }) => {
            return h(
              'a',
              {
                props: {
                  href: 'javascript: void(0)'
                },
                on: {
                  click: () => {
                    if (!row.leaf) {
                      this.$router.push({ name: 'LawItem', params: { lawId: this.$store.state.law.currentLaw.id, parentId: row.id } })
                      this.$store.commit('law/navPush', { title: row.title, path: { name: 'LawItem', params: { lawId: this.$store.state.law.currentLaw.id, parentId: row.id } } })
                    } else {
                      this.showLawItem(row)
                    }
                  }
                }
              },
              [
                h(
                  'span',
                  row.leaf ? '#' : '@'
                ),
                h(
                  'span',
                  {
                    style: {
                      marginLeft: '0.2rem'
                    }
                  },
                  row.title
                )
              ]
            )
          }
        },
        {
          title: '操作',
          render: (h, { column, row, index }) => {
            return h(
              'span',
              [
                h(
                  'Button',
                  {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    nativeOn: {
                      click: () => {
                        console.log(row)
                        this.editLawItem(row)
                      }
                    }
                  },
                  '编辑'
                ),
                h(
                  'Button',
                  {
                    props: {
                      type: 'error',
                      size: 'small'
                    },
                    style: {
                      marginLeft: '0.5rem'
                    },
                    nativeOn: {
                      click: () => {
                        this.deleteLawItem(row, index)
                      }
                    }
                  },
                  '删除'
                )
              ]
            )
          }
        }
      ],
      addLawChapterModal: false,
      lawChapterData: this.deepCopy(LawItemModel),
      addLawItemModal: false,
      lawItemData: this.deepCopy(LawItemModel),
      editLawItemFlag: false
    }
  },
  created () {
    this.getLawItemList()
    this.$store.commit('law/setChapter', this.$route.params.parentId === -1 ? null : this.$route.params.parentId)
    this.$store.commit('law/subscribe', {
      name: 'reload-law-item',
      handler: () => {
        this.getLawItemList(this.$route.params.lawId, this.$route.params.parentId)
      }
    })
  },
  beforeDestroy () {
    this.$store.commit('law/unSubscribe', 'reload-law-item')
  },
  watch: {
    '$route.params.parentId': {
      handler () {
        this.getLawItemList()
        this.$store.commit('law/setChapter', this.$route.params.parentId === -1 ? null : this.$route.params.parentId)
      },
      deep: true
    }
  },
  methods: {
    getLawItemList () {
      getLawItemList({
        lawId: this.$route.params.lawId,
        parentId: this.$route.params.parentId
      }).then(({ data, response }) => {
        if (response.success) {
          this.lawItemList = data
        } else {
          this.$Message.error(response.msg)
        }
      })
    },
    editLawItem (row) {
      if (row.leaf) {
        this.lawItemData = row
        this.addLawItemModal = true
        this.editLawItemFlag = true
      } else {
        this.lawChapterData = row
        this.addLawChapterModal = true
      }
    },
    addLawChapter (parent = null) {
      this.addLawChapterModal = true
    },
    saveChapter () {
      this.lawChapterData.lawId = this.$store.state.law.currentLaw.id
      this.lawChapterData.parentId = this.$store.state.law.currentChapter || null
      this.lawChapterData.leaf = 0
      saveLawItem(this.lawChapterData).then(({ data, response }) => {
        if (response.success) {
          this.addLawChapterModal = false
          this.lawChapterData = this.deepCopy(LawItemModel)
          this.getLawItemList()
        }
      })
    },
    addLawItem () {
      this.addLawItemModal = true
      this.editLawItemFlag = true
    },
    saveLawItem () {
      this.lawItemData.lawId = this.$store.state.law.currentLaw.id
      this.lawItemData.parentId = this.$store.state.law.currentChapter || null
      this.lawItemData.leaf = 1
      saveLawItem(this.lawItemData).then(({ data, response }) => {
        if (response.success) {
          this.addLawItemModal = false
          this.lawItemData = this.deepCopy(LawItemModel)
          this.getLawItemList()
        }
      })
    },
    deleteLawItem (row, index) {
      this.$Modal.confirm({
        title: '提示',
        content: '删除后将无法恢复，请确定是否删除！',
        onCancel: () => {},
        onOk: () => {
          deleteLawItem({
            id: row.id
          }).then(({ data, response }) => {
            if (response.success) {
              this.$Message.success('删除成功！')
              this.lawItemList.splice(index, 1)
            } else {
              this.$Message.error(response.msg)
            }
          })
        }
      })
    },
    showLawItem (law) {
      this.lawItemData = law
      this.addLawItemModal = true
    },
    closeModal () {
      this.addLawChapterModal = false
      this.addLawItemModal = false
      this.editLawItemFlag = false
    }
  }
}
</script>

<style lang="stylus" scoped>
.law-list
  background none
</style>
